import "./styles.css";
import DeleteIcon from "./DeleteIcon";
import VisibilityIcon from "./VisibilityIcon";

function formatDate(timestamp) {
  const d = new Date(timestamp);
  return d.toLocaleString();
}

const ExpenseRow = ({ item }) => {
  return (
    <div className="card my-1 mx-2 item">
      <div className="card-body">
        <div className="row">
          <div className="col-8">
            <h6 className="card-title mb-0">
              {item.currency} <strong>{item.amount}</strong>
            </h6>
            <span className="badge bg-light text-dark p-0 text-small">
              {formatDate(item.timestamp)}
            </span>
            <p className="card-text text-medium">{item.name}</p>
          </div>
          <div className="col-4 d-flex justify-content-end align-items-center">
            <VisibilityIcon id={item.id} isPrivate={item.private} />
            &nbsp;
            <DeleteIcon id={item.id} />
          </div>
        </div>
      </div>
    </div>
  );
};

export default ExpenseRow;
